import { makeStyles, Tooltip, Fab, Modal, Container, TextField, MenuItem, RadioGroup, FormControlLabel, Radio, FormLabel, FormControl, Button } from "@material-ui/core";
import { Add as AddIcon } from "@material-ui/icons";
import { useState } from "react";
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const myStyles = makeStyles((theme) => ({
    fab: {
        position: "fixed",
        bottom: "20px",
        left: "20px"
    },
    container: {
        width: 500,
        height: 550,
        backgroundColor: "white",
        margin: "auto",
        position: "absolute",
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
        [theme.breakpoints.down("sm")]: {
            height: "100vh",
            width: "100vw"
        }
    },
    form: {
        padding: theme.spacing(2),
    },
    item: {
        marginBottom: theme.spacing(2)
    },
    select: {
        width: 200
    }
}))

function Add() {
    const myStyle = myStyles();
    const [open, setOpen] = useState(false);
    const [value, setValue] = useState('forfriends');

    const [openAlert, setAlert] = useState(false);

  const handleClick = () => {
    setAlert(true);
  };

  const handleClose = (event, reason) => {
    //if (reason === 'clickaway') {
        setAlert(false);
    //}
  };

    const handleChange = (event) => {
        setValue(event.target.value);
    };


    return (
        <>
            <Tooltip title="Add" aria-label="افزودن پست جدید" onClick={() => setOpen(true)}>
                <Fab color="primary" className={myStyle.fab}>
                    <AddIcon />
                </Fab>
            </Tooltip>
            <Modal open={open} >
                <Container className={myStyle.container}>
                    <form className={myStyle.form} autoComplete="off">
                        <div className={myStyle.item}>
                            <TextField id="outlined-basic" variant="outlined" placeholder="عنوان" />
                        </div>
                        <div className={myStyle.item}>
                            <TextField
                                id="outlined-multiline-static"
                                placeholder="محتوی"
                                multiline
                                rows={4}
                                defaultValue="یک سری محتوی"
                                variant="outlined"
                                style={{ width: "100%", direction: "rtl" }}
                            />
                        </div>
                        <div className={myStyle.item}>
                            <TextField select label="نوع دسترسی" variant="outlined" className={myStyle.select}>
                                <MenuItem value="public">عمومی</MenuItem>
                                <MenuItem value="private">خصوصی</MenuItem>
                                <MenuItem value="friends">دوستان</MenuItem>
                            </TextField>
                        </div>
                        <div className={myStyle.item}>
                            <FormControl component="fieldset">
                                <FormLabel component="legend">دسترسی کامنت گذاری:</FormLabel>
                                <RadioGroup aria-label="gender" name="gender1"  value={value} onChange={handleChange} >
                                    <FormControlLabel value="forfriends" control={<Radio size="small" />} label="برای دوستان" />
                                    <FormControlLabel value="forall" control={<Radio size="small" />} label="برای همه" />
                                    <FormControlLabel value="fornoone" control={<Radio size="small" />} label="هیچ کس" />
                                </RadioGroup>
                            </FormControl>
                        </div>
                        <div className={myStyles.item}>
                            <Button variant="outlined" color="primary" style={{marginLeft: "10px"}} onClick={() => setAlert(true)}>ارسال</Button>
                            <Button variant="outlined" color="secondary" onClick={() => setOpen(false)}>انصراف</Button>
                        </div>
                    </form>
                </Container>
            </Modal>
            <Snackbar open={openAlert} autoHideDuration={6000} onClose={handleClose} anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}>
                <Alert onClose={handleClose} severity="success">
                    ارسال با موفقیت انجام شد
                </Alert>
            </Snackbar>
        </>
    );
}

export default Add;
