import { Container, makeStyles } from "@material-ui/core";
import Post from "./Post";


const myStyles = makeStyles((theme) => ({
    container: {
        paddingTop: theme.spacing(2)
    }

}))

function Feed() {
  const myStyle = myStyles();
  return (
    <Container className={myStyle.container}>
        <Post />
        <Post />
        <Post />
        <Post />
        <Post />
        <Post />
        <Post />
        <Post />
        <Post />
        <Post />
        <Post />
    </Container>
  );
}

export default Feed;
